<template>
  <div class="item_container">
    <a v-bind:href="item.url" class="item-wrapper" title="点击进入">
      <img v-show="item.imgUrl" v-bind:src="item.imgUrl" class="item_img">
      <h2 :class="item.imgUrl ? 'item_title' : 'item_title_nopic' ">{{ item.title }}</h2>
      <p class="item_extrainfo">{{ item.extraText }}</p>
    </a>
  </div>
</template>
<script>
  export default{
    name:'list-item',
    props:{
      item: {
        title: String,
        url: String,
        imgUrl:{
          type: String,
          default: ''
        },
        extraText:{
          type: String,
          default: ''
        },
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/mixin";
  @import "../assets/config.scss";
  .item_container {
    @include wh(500px, 140px);
    margin-bottom: 10px;
    background-color: transparent;
    border: 2px solid #fff;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
    .item-wrapper {
      @include wh();
      @include positionTL();
      .item_img {
        @include wh(140px, 140px);
        @include positionTL();
        background-size: cover;
        background: center no-repeat;
      }
      .item_title {
        @include positionTL(absolute, 20px, 160px);
        color: white;
        font-size: $font20;
      }
      .item_title_nopic {
        @include positionTL(absolute, 20px, 20px);
        color: white;
        font-size: $font20;
      }
      .item_extrainfo {
        @include positionBR(absolute, 20px, 20px);
        color: white;
        font-size: $font12;
      }
    }
  }
</style>
